
.c-sidebar{display:block
}
.c-sidebar ul,.c-sidebar li{list-style-type:none
}
.c-sidebar .sidebar-inner{list-style:none;margin:0 0 0 0;padding:0
}
.c-sidebar .sidebar-inner>ul{padding:0
}
.c-sidebar .sidebar-inner>ul>li{display:block;margin:0;padding:0;border:0
}
.c-sidebar .sidebar-inner>ul>li.heading{height:50px;padding:25px 15px 10px 15px
}
.c-sidebar .sidebar-inner>ul>li.heading>h3{font-size:13px;padding:0;margin:0;font-weight:600
}
.c-sidebar .sidebar-inner>ul>li>a{display:block;position:relative;margin:1px 0 0 0;border:0;padding:12px 12px;text-decoration:none;font-size:.9rem
}
.c-sidebar .sidebar-inner>ul>li>a>.title{font-weight:300
}
.c-sidebar .sidebar-inner>ul>li>a>.arrow,.c-sidebar .sidebar-inner>ul>li>a>.title{transition:opacity 300ms linear;opacity:1
}
.c-sidebar .sidebar-inner>ul>li>a>i{top:3px;font-size:15px;font-weight:300;width:30px;text-align:center;text-shadow:none
}
.c-sidebar .sidebar-inner>ul>li.open>a{font-size:.9rem
}
.c-sidebar .sidebar-inner>ul>li.active>a{border:none;text-shadow:none;font-size:.9rem
}
.c-sidebar .sidebar-inner li>a>.arrow:before{width:10px;float:right;margin-right:5px;margin-left:5px;display:inline;font-size:.9rem;font-family:FontAwesome;height:auto;content:"\f104";font-weight:300;text-shadow:none
}
.c-sidebar .sidebar-inner li>a>.arrow.open:before{content:"\f107";margin-right:6px
}
.c-sidebar .sidebar-inner li>a>.badge{float:right;margin-top:1px;margin-right:0
}
.c-sidebar .sidebar-inner .sub-menu{list-style:none;padding:0;margin:8px 0 8px 0
}
.c-sidebar .sidebar-inner .sub-menu li{background:none;margin:0;padding:0;margin-top:1px !important
}
.c-sidebar .sidebar-inner .sub-menu li>a{display:block;margin:0;padding:9px 14px 9px 30px;text-decoration:none;font-size:.9rem;font-weight:300;background:none
}
.c-sidebar .sidebar-inner .sub-menu li>a>i{font-size:.9rem;margin-right:1px;width:30px;text-align:center
}
.c-sidebar .sidebar-inner .sub-menu li>a .arrow.open:before{margin-top:-2px
}
.c-sidebar .sidebar-inner .sub-menu li>.sub-menu{margin:0
}
.c-sidebar .sidebar-inner .sub-menu li>.sub-menu>li>a{padding-left:40px
}
.c-sidebar .sidebar-inner .sub-menu li>.sub-menu>li>.sub-menu{margin:0
}
.c-sidebar .sidebar-inner .sub-menu li>.sub-menu>li>.sub-menu>li>a{padding-left:50px
}
.sidebar-closed .c-sidebar .sidebar-inner,.sidebar-hidden .c-sidebar .sidebar-inner{display:none
}
@media (min-width: 768px){
.c-sidebar{display:block
}
.c-sidebar .sidebar-inner{width:250px;float:left;position:relative;margin-right:-100%;transition:width 300ms linear
}
.sidebar-closed .c-sidebar .sidebar-inner{display:block;width:54px
}
.sidebar-closed .c-sidebar .sidebar-inner>ul>li>a{z-index:999
}
.sidebar-closed .c-sidebar .sidebar-inner>ul>li>a>.title,.sidebar-closed .c-sidebar .sidebar-inner>ul>li>a>.arrow{transition:opacity 300ms linear;opacity:0;display:none
}
.sidebar-closed .c-sidebar .sidebar-inner>ul>li:hover>a{width:250px
}
.sidebar-closed .c-sidebar .sidebar-inner>ul>li:hover>a>.title,.sidebar-closed .c-sidebar .sidebar-inner>ul>li:hover>a>.arrow{opacity:1;display:initial
}
.sidebar-closed .c-sidebar .sidebar-inner>ul>li:hover.open .sub-menu{z-index:999;display:block;position:absolute;left:54px;background:white;width:196px;margin:0
}
.sidebar-closed .c-sidebar .sidebar-inner>ul>li.open .sub-menu{display:none
}
.sidebar-closed.sidebar-compact .c-sidebar .sidebar-inner{width:45px
}
.sidebar-closed.sidebar-compact .c-sidebar .sidebar-inner>ul>li.open:hover .sub-menu{left:45px;width:205px
}
.sidebar-compact .c-sidebar .sidebar-inner>ul>li>a{padding:6px 6px
}
.sidebar-compact .c-sidebar .sidebar-inner .sub-menu{margin:2px 0 4px 0
}
.sidebar-compact .c-sidebar .sidebar-inner .sub-menu li>a{padding-top:6px;padding-bottom:6px;padding-right:6px
}
}



